import { config } from "../config/corrector";
import { MatchesEntity, Rule } from "./types";

export class MatchesRepository {
    private matches : MatchesEntity[] = []

    public setMatches(matches: MatchesEntity[]) {
        this.matches = matches
    }

    public reset() {
        this.matches = []
    }

    public hasMatches() {
        return this.matches.length > 0
    }

    public getAll(): MatchesEntity[] {
        return this.matches || []
    }

    public delete(applied: string) {
        this.matches = this.matches.filter(m => m.uuid !== applied)
    }

    public update(applied: string, length: number) {
        let startingOffset = -1
        let difference = 0
        let found = false

        for(const match of this.matches) {
            if(match.uuid === applied) {
                found = true
                startingOffset = match.offset
                difference = length - match.length
                break
            }
        }

        if (found) {
            this.processChanges({ position: startingOffset + 1, difference})
            this.matches = this.matches.filter(m => m.uuid !== applied)
        }
    }

    public processChanges({ position, difference } : SizeChange) {
        this.matches?.filter(m => m.offset >= position)
            ?.forEach(m => m.offset += difference)
    }
    

    public get(uuid: string) {
        return this.matches.find(m => m.uuid === uuid)
    }
    
}

export type SizeChange = {
    position: number
    difference: number
}

export const getMaxSuggestions = (rule : Rule) => {
    if (["mispelling", "locale-violation"].includes(rule.issueType)){
        return config.maxSpellcheckSuggestions
    }

    if (document?.getElementById('editor-area')?.offsetWidth || 0 < 400) {
        return config.maxSuggestionsMobile
    }

    return config.maxSuggestions
}