import { Extension, getText, getTextSerializersFromSchema, Range } from '@tiptap/core'
import { Plugin, PluginKey, Transaction } from '@tiptap/pm/state'
import { Decoration, DecorationSet, EditorView } from '@tiptap/pm/view'
import { Editor } from '@tiptap/core'
import { MatchesRepository, SizeChange } from '../languagetool/MatchesRepository'
import { EditorMatch, MatchesEntity } from '../languagetool/types'
import { TextMapper } from '../prosemirror/TextMapper'
import { StepMap } from '@tiptap/pm/transform'
import { Node, Schema } from '@tiptap/pm/model'

declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        languagetool: {
            showMatches: () => ReturnType
        }
    }
}

type ExtensionOptions = {
    matchesRepository: MatchesRepository
}

type LanguageToolExtensionStorage = {
    currentMatch : EditorMatch
}

type LoggedMatch = {
    time: number
    match: MatchesEntity | null
}

export const REFRESH_MATCHES = 'sc-languagetool-showmatches'
const DOC_SCHEMA = 'document-schema'
export const SKIP_PROCESSING = "skip-processing-offsets"
const LAST_CLICK_TIMESTAMP = "update-click"

let globalEditor : Editor

export const LanguageToolExtension = Extension.create<ExtensionOptions, LanguageToolExtensionStorage>({
    name: 'languagetool',
    priority: 1000,

    addOptions() {
        return {
            matchesRepository: new MatchesRepository(),
        }
    },

    addStorage() {
        return {
            currentMatch: new EditorMatch()
        }
    },

    addCommands() {
        return {
            showMatches: () => ({
                editor,
                tr,
            }) => {

                if (!globalEditor) {
                    globalEditor = editor
                }

                tr.setMeta(REFRESH_MATCHES, true)
                tr.setMeta(DOC_SCHEMA, editor.schema)

                return true
            }
        }
    },

    addProseMirrorPlugins() {
        const matchesRepository = this.options.matchesRepository
        const storage = this.storage
        return [

            new Plugin({
                key: new PluginKey("matchesDecorations"),
                state: {
                    init: () => MatchesState.create(matchesRepository),
                    apply(tr, prev) {
                        return prev.apply(tr);
                    }
                },
                view: () => ({
                    update: () => {
                      
                    },
                }),
                props: {
                    decorations(state) { return this.getState(state)?.decorations },
                    handleClickOn(view: EditorView,
                        _pos: number,
                        _node: Node,
                        _nodePos: number,
                        event: MouseEvent) {


                        if (!globalEditor) {
                            return
                        }

                        const dispatch = (key : string, value: LoggedMatch) => {
                            globalEditor.commands.command(({state, dispatch}) => {
                                dispatch!(state.tr.setMeta(key, value))
                                return true
                            })
                        }

                        const matchId = (event.target as HTMLSpanElement).getAttribute('data-match-id')?.trim()                   
                        if (!matchId) {
                            storage.currentMatch.clear()
                            dispatch(LAST_CLICK_TIMESTAMP, { time: Date.now(), match: null})
                            return
                        }

                        const match = matchesRepository.get(matchId)
                        
                        if (!match) {                           
                            storage.currentMatch.clear()
                            dispatch(LAST_CLICK_TIMESTAMP, { time: Date.now(), match: null})
                            
                            return true
                        } else {
                            const state = view.state
                            const schema = state.schema
                            const text = getText(state.doc, { textSerializers: { ...getTextSerializersFromSchema(schema) } })
                    
                            const textMapper = new TextMapper(state.doc, text, { schema: schema})
                            const range = textMapper.getRange(match.offset, match.length)

                            storage.currentMatch.update(match, matchId, range!)

                            dispatch( LAST_CLICK_TIMESTAMP, { time: Date.now(), match})
                            return true
                        }
                    },
                    handlePaste() {
                        if (!globalEditor) {
                            return
                        }

                        const dispatch = (key : string, value: LoggedMatch) => {
                            globalEditor.commands.command(({state, dispatch}) => {
                                dispatch!(state.tr.setMeta(key, value))
                                return true
                            })
                        }

                        matchesRepository.reset()
                        dispatch(LAST_CLICK_TIMESTAMP, { time: Date.now(), match: null})
                    }
                },
            })
        ]
    },
})

class MatchesState {
    
    private schema?: Schema
    
    private match? : MatchesEntity

    constructor(public decorations: DecorationSet, private matchesRepository: MatchesRepository) { 
        this.match = undefined
    }
    
    getSchema() {
        return this.schema
    }
    
    apply(tr: Transaction) {

        if (!this.schema) {
            if (!tr.getMeta(DOC_SCHEMA)) {
                return this
            } else {
                this.schema = tr.getMeta(DOC_SCHEMA)
            }
        }

        let isNewMatch = false

        if (tr.getMeta(LAST_CLICK_TIMESTAMP)) {
            const match = tr.getMeta(LAST_CLICK_TIMESTAMP).match

            if (match !== this.match) {
                this.match = match
                isNewMatch = true
            }
        }

        if (!tr.docChanged && !isNewMatch) {
            if (!this.matchesRepository.hasMatches()) {
                return this
            }

            if (!tr.getMeta(REFRESH_MATCHES)) {
                return this
            }
        }

        this.processChanges(tr)

        this.createDecorations(tr)

        return this
    }

    private createDecorations(tr: Transaction) {
        const text = this.getText(tr.doc)
        const textMapper = new TextMapper(tr.doc, text, { schema: this.schema })

        const matches = this.matchesRepository.getAll()

        const decorations: Decoration[] = []

        this.decorations = this.decorations.remove(this.decorations.find())

        if (!matches?.length) {
            return
        }

        for (const match of matches) {

            const range = textMapper.getRange(match.offset, match.length)

            if (range) {
                decorations.push(decorate(range, match))
            }
        }


        if (!decorations.length) {
            this.decorations = DecorationSet.empty
            return
        }

        this.decorations = this.decorations.add(tr.doc, decorations)
    }

    processChanges(tr: Transaction) {

        if (!tr.docChanged || tr.getMeta(SKIP_PROCESSING) || tr.getMeta(LAST_CLICK_TIMESTAMP)) {
            return
        }

        const oldText = this.getText(tr.before)
        const newText = this.getText(tr.doc)

        if (oldText === newText) {
            return
        }

        const mappingFrom = tr.mapping.from
        const mappingTo = tr.mapping.to

        const changes : SizeChange[]= []

        const textMapper = new TextMapper(tr.before, oldText, { schema: this.schema })

        for (let i = mappingFrom; i < mappingTo; i++) {
            const mapping: StepMap = tr.mapping.maps[i]

            mapping.forEach((oldStart, oldEnd, newStart, newEnd) => {
                if (oldEnd - oldStart !== newEnd - newStart) {
                    const textPosition = textMapper.getTextOffset(oldStart)
                    if (textPosition > -1) {
                        changes.push({
                            position: textPosition,
                            difference: newEnd - oldEnd
                        })
                    }
                }
            })
        }

        if (changes.length) {
            if (changes.length > 1) {
                console.warn("There should be only one change", changes)
            }
            this.matchesRepository.processChanges(changes[0])
        }
    }

    public getText(doc: Node) {
        if (!this.schema) {
            return ""
        }
        return getText(doc, { textSerializers: { ...getTextSerializersFromSchema(this.schema) } })
    }

    static create(matchesRepository: MatchesRepository) {
        return new MatchesState(DecorationSet.empty, matchesRepository)
    }
}

const decorate = (range: Range, match: MatchesEntity) =>
    Decoration.inline(range.from, range.to, {
        class: `lt lt-${match.rule.issueType}`,
        nodeName: 'span',
        'data-match-id': match.uuid,
    })
