import { ChangeEvent } from "react"
import { Variants } from "../config/types"
import { useLanguageTool } from "../hooks/useLanguageTool"
import { CheckStatus } from "../hooks/LanguageTool"
import classNames from "classnames"

type VariantToolbarProps = {
    action: () => void
}

const VariantToolbar = (props: VariantToolbarProps) => {

    const { variant, status, setVariant } = useLanguageTool()

    function handleChange(e : ChangeEvent<HTMLInputElement>) {
        setVariant(e.target.value as Variants)
    }

    let disabled = status == CheckStatus.Checking

    const btnClasses = classNames({
        'bt-basic': true,
        "bt-basic-vermell": !disabled,
        "bt-basic-gris": disabled,
        'submit': true
      });

    return (<div className="btns-corrector row">
        <div className="col-xs-12">
            <div className="formes">
                <p>Formes verbals:</p>
                <label className="radio-inline">
                    <input type="radio" name="variant" id="variant_general" value="variant_general"
                        checked={variant === Variants.General} onChange={handleChange}
                        /> centrals
                </label>
                <label className="radio-inline">
                    <input type="radio" name="variant" id="variant_valencia" value="variant_valencia"
                        checked={variant === Variants.Valencianes} onChange={handleChange}
                        /> valencianes
                </label>
                <label className="radio-inline">
                    <input type="radio" name="variant" id="variant_balear" value="variant_balear"
                        checked={variant === Variants.Balears} onChange={handleChange}
                        /> balears
                </label>
            </div>

            <div className="btn-right">
                <input disabled={disabled} type="submit" className={btnClasses} name="_action_checkText" value="Revisa" 
                    onClick={props.action}
                />
            </div>
        </div>
    </div>)
}
  

export default VariantToolbar
