import { useEditor, EditorContent } from '@tiptap/react'
import { Editor } from '@tiptap/core'
import { useState } from 'react'
import Menu from './Menu'
import extensions from '../config/extensions'
import VariantToolbar from './VariantToolbar'
import { useLanguageTool } from '../hooks/useLanguageTool'
import { LanguageToolExtension } from '../extensions/languageTool'
import StatusBar from './StatusBar'
import { SuggestionBox } from './suggestions/SuggestionBox'
import { EditorMatch } from '../languagetool/types'
import { cleanSuggestion, replaceSuggestion } from './suggestions/replacer'
import { OptionSection } from './OptionSection'
import { LinkEditor } from './Link/LinkEditor'
import { LinkContextProvider } from '../hooks/LinkContext'
import { Metrics, getMetrics } from '../metrics'
import { CheckStatus } from '../hooks/LanguageTool'
import SpecialMenu from './SpecialMenu'

const SoftcatalaEditor = (props: { content: string }) => {

    const { getMatches, status, setStatus, matchesRepository } = useLanguageTool()
    const [metrics, setMetrics] = useState<Metrics|null|undefined>(undefined)

    const [currentMatch, setCurrentMatch] = useState<EditorMatch>(new EditorMatch())

    const updateMatch = (editor: Editor) => {
        setCurrentMatch(editor.storage.languagetool.currentMatch)
    }

    const editor = useEditor({
        extensions: [
            LanguageToolExtension.configure({
                matchesRepository,
            }),
            ...extensions,
        ],
        content: props.content,
        onUpdate({ editor }) {
            setTimeout(() => updateMatch(editor))
            setTimeout(() => !!metrics && setMetrics({...metrics, dirty: true}))
        },
        onSelectionUpdate({ editor }) {
            setTimeout(() => updateMatch(editor))
        },
    })

    if (!editor) {
        return <></>
    }

    const triggerSpellCheck = async () => {
        setStatus(CheckStatus.Checking)
        const text = editor.getText()
        editor.setOptions({ editable: false})

        await Promise.all([getMatches(text), getMetrics(text, setMetrics)])
        
        editor.commands.showMatches()
        setStatus(CheckStatus.Idle)
        editor.setOptions({ editable: true})
        editor.commands.focus()
    }

    const replace = (replacement: string) => {
        replaceSuggestion(editor, matchesRepository, currentMatch, replacement)
        setCurrentMatch(new EditorMatch())
    }

    const cleanCurrentErrorInstance = () => {
        cleanSuggestion(editor, matchesRepository, currentMatch)
        setCurrentMatch(new EditorMatch())
    }

    const cleanEditor = () => {
        matchesRepository.reset()
        editor.commands.clearContent(true)
    }

    const sampleText = () => {
        matchesRepository.reset()
        editor.commands.setContent(props.content)
    }

    const getState = () => {
        return editor.state.doc
    }

    const getText = () => {
        return editor.getText()
    }

    return (
        <>
            <VariantToolbar action={triggerSpellCheck} />
            <LinkContextProvider editor={editor}>
                <div className='editor' id="editor-area">
                    <SpecialMenu getState={getState} getText={getText} clean={cleanEditor} sampleText={sampleText}/>
                    <Menu editor={editor} />
                    <EditorContent spellCheck={false} editor={editor} />
                    <StatusBar editor={editor} metrics={metrics} status={status} />
                    <SuggestionBox 
                        currentMatch={currentMatch} 
                        editor={editor}
                        replace={replace}
                        clean={cleanCurrentErrorInstance}
                    />
                </div>
                { editor ? <LinkEditor editor={editor} /> : <></> }
            </LinkContextProvider>
            <OptionSection />
        </>
    )
}

export default SoftcatalaEditor