import { memo } from "react";
import MenuItemDivider from "./MenuItemDivider";
import SaveAs from "./SaveAs";
import { Node } from "@tiptap/pm/model";

const EXPORT_RICH_FORMAT = false

type SpecialMenuProps = {
    clean: () => void,
    sampleText: () => void
    getState: () => Node
    getText: () => string
}

const SpecialMenu = ({ clean, sampleText, getState, getText } : SpecialMenuProps) => {
    return (<div className="menu special-menu">
                <Exporta getState={getState} />
                <span id="copy-text" className="icon-inline" onClick={() => {
                    navigator.clipboard.writeText(getText())
                }}>
                    <i className="fa fa-clipboard" aria-hidden="true"></i> Copia el text pla
                </span>
                <MenuItemDivider />
                <span id="clear_text" onClick={clean} className="icon-inline">
                    <i className="fa fa-eraser" aria-hidden="true"></i> Neteja
                </span>
                <MenuItemDivider />
                <a id="text_prova" onClick={sampleText}>Text de prova</a>
    </div>)
}

const Exporta = ({ getState } : { getState: () => Node}) => {
    return EXPORT_RICH_FORMAT ? <>
                    <SaveAs format="odt" getState={getState} />
                <SaveAs format="docx" getState={getState} />
                <MenuItemDivider />
    </> : <></>
}

const MemoizedSpecialMenu = memo(SpecialMenu)

export default MemoizedSpecialMenu
